.Browser {
    width: calc(100vw - 200px);
    height: calc(100% - 100px);
    margin: auto auto;
    background-color: white;
    display: flex;
    flex-direction: column;
    border-radius: 6px;
}
.placeholder {
    height: 50px;
}
.flexGrow {
    margin-right:auto;
}
.tabRowIconsCorner {
    margin-left: 32px;
    margin-top: 12px;
    color: rgba(0, 0, 0, 0.6);

    &:last-of-type {
        margin-right: 18px;
    }
}
.urlField {
    display:flex;
    flex-direction: row;
    align-items: center;
    min-width: 150px;
    width: 60%;
    height: 34px;
    max-width: 1200px;
    border-radius: 2em;
    border:none;
    background-color: rgba(194, 218, 208, 0.25)
}
.urlIcon {
    &:first-of-type {
        margin-left: 12px;
        color:rgba(0, 0, 0, 0.5);
    }
    &:last-of-type {
        margin-left: auto;
        margin-right: 12px;
        font-size:18px;
        color:rgba(0, 0, 0, 0.5);
    }
}
.tabInactive {
    cursor:pointer!important;
    &::after {
        content:"";
        position:absolute;
        right:-3px;
        top:10%;
        bottom:30%;
        border-right:1px solid rgba(0, 0, 0, 0.35);
    }
    &:hover {
        @extend .tab_active;
    }
}
.tabInactive:hover::after {
    border-right:none;
}
.tabRow {
    position: relative;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    background-color: lightgray;
    display:flex;
    flex-direction: row;
}
.tabPlus {
    margin-left: 24px;
    margin-top: 16px;
    color: rgba(0, 0, 0, 0.6);
}
.tab {
    color: rgba(0, 0, 0, 0.6);
    min-width: 100px;
    width: 20%;
    max-width: 200px;
    min-height:44px;
    margin-top: 8px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    &:first-of-type {
        margin-left:12px;
    }
}
.tabContent {
    margin-bottom: 10px;
}
.tabText {
    margin-left: 18px;
}
.closeTab {
    margin: auto;
    text-align: center;
    font-size:18px;
    z-index:5;
}
.closeTabDiv {
    cursor: pointer;
    margin-left: auto;
    margin-right: 13px;
    width:22px;
    height:22px;
    border-radius: 50%;
    display:flex;
    align-items:center;
}
.closeTabDiv:hover {
    background-color: rgba(0,0,0,0.15);
}
.tab_active {
    cursor: default;
    background-color: #fff;
    border-radius: 10px;
    position: relative;
}
.tab_active::before {
    content: "";
    position: absolute;
    background-color: transparent;
    bottom: 50px;
    left:-12px;
    top: -14px;
    transform:rotate(180deg);
    height: 50px;
    width: 15px;
    border-top-left-radius:50px;
    box-shadow: -1px -15px 0 0 #fff;
  }
  .tab_active::after {
    content: "";
    position: absolute;
    background-color: transparent;
    bottom: 50px;
    right: -13px;
    top: -14px;
    transform: rotate(180deg);
    height: 50px;
    width: 15px;
    border-top-right-radius: 50px;
    box-shadow: 0px -15px 0 0 #fff;
  }
.top {
    min-height:44px;
    max-height:44px;
}
.addressRow {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #fff;
    z-index: 10;
    border-bottom: 1px solid rgba(0, 0, 0, 0.35);
}
.addressRowRight {
    position: relative;
    @extend .addressRow;
    border: none;
    margin-left: auto;
    &:last-child {
        margin-right: 6px;
    }
}
.userCircle {
    cursor: pointer;
    border-radius: 50%;
    margin-right:24px;
    transition-duration: 400ms;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 50%;
    // font-size: 40px;
    color: #fff;
    text-align: center;
    background: #0e153a
}
.userCircle:hover {
    -webkit-box-shadow: 0px 0px 29px 2px rgba(34,209,238,1);
    -moz-box-shadow: 0px 0px 29px 2px rgba(34,209,238,1);
    box-shadow: 0px 0px 29px 2px rgba(34,209,238,1);
}
.addressIcons {
    font-size: 20px;
    margin-right: 24px;
    color: rgba(0, 0, 0, 0.6);
    &:first-of-type {
        margin-left: 24px;
    }
}
.nameBoxOpen {
    display: flex!important;
    flex-direction: column;
    align-items: center;
}
.nameBox {
    display: none;
    position: absolute;
    z-index: 10;
    top: 34px;
    // border: 1px solid rgba(0, 0, 0, 0.35);
    border-radius: 4px;
    right: 42px;
    width: 300px;
    height: 400px;
    background-color: white;
    box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12);
}
.nameBoxCircle {
    margin-top: 24px;
    width: 80px;
    height: 80px;
    line-height: 80px;
    border-radius: 50%;
    font-size: 40px;
    color: #fff;
    text-align: center;
    background: #0e153a;
}
.nameBoxName{
    font-size: 22px;
    margin-top: 8px;
}
.nameBoxTitle{

}
.content {
    width:100%;
    height: 100%;
    overflow-y: auto;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    background: #6AEAFF;
    background: -webkit-linear-gradient(bottom right, #6AEAFF, #D9FAFF);
    background: -moz-linear-gradient(bottom right, #6AEAFF, #D9FAFF);
    background: linear-gradient(to top left, #6AEAFF, #D9FAFF);
    box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12);
}